.sub-projects {
  display: grid;
  grid-template-columns: repeat(3, 0fr);
  justify-content: center;
  align-items: center;
  gap: 3rem;
  margin-top: 4rem;
  margin-bottom: 2rem;
}
.project_box {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid #8e79ff;
  width: max-content;
  padding: 1rem;
  border-radius: 5px;
  transition: 1s;
}
.project_box:hover {
  box-shadow: 0px 0px 4px 2px #7c67f1;
  scale: 1.1;
  background: linear-gradient(45deg, black, transparent);
}
.project_img {
  width: 17rem;
}
h5 {
  font-size: 1.4rem;
  font-family: sans-serif;
}
.btns {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.github {
  background: #713e9b;
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  border: 0px solid;
  transition: 0.1s;
}
.github:hover {
  background: #6900bf;
  scale: 1.1;
}
.demo {
  background: #713e9b;
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  border: 0px solid;
  transition: 0.1s;
}
.demo:hover {
  background: #6900bf;
  scale: 1.1;
}
